/*************************************
WordPress Specifics
**************************************/
.alignleft,
.alignright {
  display: inline-block;
}

.alignleft {
  float: left;
  margin: 0 20px 5px 0;
}

.alignright {
  float: right;
  margin: 0 0 5px 20px;
}

.alignnone {
  margin: 5px 15px 15px 0;
}

.aligncenter {
  display: block;
  margin: 15px auto;
}

.wp-caption {
  background: rgba(0, 0, 0, 0.1);
  border: 1px rgba(0, 0, 0, 0.3) solid;
  padding: 15px;
  font-size: 0.85em;
  text-align: center;
}
.wp-caption img {
  border: 1px rgba(0, 0, 0, 0.3) solid;
  margin: 0 auto 15px;
  max-width: 98.5%;
